<template>
  <div class="foucs">
    <ul class="foucs-list">
      <li
        @click="itemClick(index)"
        v-for="index in pageN"
        :key="index"
        :class="{ active: index === currentIndex }"
      >
        <span></span>
      </li>
    </ul>
    <div class="mplay" ref="mplay" @click="mplaySwitch">
      <audio loop ref="audio" autoplay @load="audioLoad">
        <source
          src="~assets/music/追忆冰袋辣条 - Story Of A Lonely Man.mp3"
          type="audio/mpeg"
        />
      </audio>
      <div ref="icon" class="iconplay">
        <span class="iconfont play" v-if="isPlay">&#xe665;</span>
        <span class="iconfont" v-else>&#xe666;</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Focus",
  data() {
    return {
      mplayStyle: {}, //样式
      Angle: 0,
      timeId: {},
      isPlay: true,
      audioEl: {},
      audioStyle: {},
      iconPlayStyle: {},
      pageN: 4,
    };
  },
  props: {
    currentIndex: {
      type: Number,
    },
  },
  methods: {
    mplay() {
      this.timeId = setInterval(() => {
        this.Angle++;
        if (this.Angle >= 360) {
          this.Angle = 0;
        }
        this.setTransform(this.Angle);
      }, 30);
    },
    handleDom() {
      let mplayEl = this.$refs.mplay;
      let iconPlayEl = this.$refs.icon;
      this.mplayStyle = mplayEl.style;
      this.audioEl = this.$refs.audio;
      this.audioStyle = this.audioEl.style;
      this.iconPlayStyle = iconPlayEl.style;
    },
    setTransform(position) {
      this.mplayStyle.transform = `rotate(${position}deg)`;
      this.mplayStyle["-webkit-transform"] = `rotate(${position}deg)`;
      this.mplayStyle["-ms-transform"] = `rotate(${position}deg)`;
      this.iconPlayStyle.transform = `rotate(${-position}deg)`;
      this.iconPlayStyle["-webkit-transform"] = `rotate(${-position}deg)`;
      this.iconPlayStyle["-ms-transform"] = `rotate(${-position}deg)`;
    },
    mplaySwitch() {
      if (this.isPlay) {
        this.isPlay = !this.isPlay;
        clearInterval(this.timeId);
        this.audioEl.pause();
      } else {
        this.isPlay = !this.isPlay;
        this.mplay();
        this.audioEl.play();
      }
    },
    audioLoad() {
      console.log("lll");
    },
    itemClick(index) {
      this.$emit('itemClick',index)
    },
  },
  mounted() {
    this.mplay();
    this.handleDom();
  },
};
</script>

<style scoped>
ul,
li {
  list-style: none;
}
.foucs {
  position: fixed;
  width: 50px;
  height: 210px;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 9;
}
.foucs-list {
  width: 50px;
  height: 160px;
  text-align: center;
}
.foucs li {
  width: 50px;
  height: 40px;
  line-height: 40px;
  text-align: center;
}
.foucs-list li span {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
  margin: auto;
}
.mplay {
  height: 50px;
  width: 50px;
  background: url(~assets/imgs/mplay.png) no-repeat 0 0;
  position: absolute;
  bottom: 0;
  border-radius: 50%;
  animation: mplay 5s infinite;
  overflow: hidden;
}
.iconplay {
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background-color: rgba(167, 167, 167, 0.5);
}
.iconfont {
  color: rgb(255, 255, 255);
  position: absolute;
  z-index: 9;
  font-size: 30px;
  position: relative;
}
.iconfont.play {
  left: 3px;
}
.active span {
  border: 4px solid deeppink;
}
</style>