<template>
  <div id="app">
    <div class="bg-skin"></div>
    <foucs :currentIndex="currentIndex" @itemClick="itemClick"></foucs>
    <div class="list" ref="list">
      <frist-page @abClick="abClick" @proClick="proClick"></frist-page>
      <two-page></two-page>
      <three-page></three-page>
      <four-page></four-page>
    </div>
  </div>
</template>

<script>
import FristPage from "components/FristPage";
import TwoPage from "components/TwoPage";
import ThreePage from "components/ThreePage";
import FourPage from "components/FourPage";
import Foucs from "./components/Focus";

export default {
  name: "App",
  data() {
    return {
      listEl: {},
      listStyle: {},
      currentIndex: 1,
      windomH: 0,
    };
  },
  components: {
    FristPage,
    TwoPage,
    ThreePage,
    FourPage,
    Foucs,
  },
  methods: {
    //按下事件处理
    keyDom(e) {
      var evt = e || event;
      this.upDown(evt.which);
    },
    //监听上下键的按下
    upDown(key) {
      if (key === 40) {
        if (this.currentIndex !== 4) {
          this.currentIndex++;
          this.downmove(this.currentIndex);
        }
      } else if (key === 38) {
        if (this.currentIndex !== 1) {
          this.currentIndex--;
          this.upmove(this.currentIndex);
        }
      }
    },
    downmove(index) {
      this.move(30, -(index - 1) * this.windomH);
    },
    upmove(index) {
      this.move(30, -(index - 1) * this.windomH);
    },
    move(topSize, top) {
      let inTop = parseInt(this.listStyle.top);
      if (parseInt(this.listStyle.top) > top) {
        clearInterval(timeid);
        let timeid = setInterval(() => {
          this.setTop(parseInt(this.listStyle.top) - topSize);
          if (parseInt(this.listStyle.top) <= top) {
            this.listStyle.top = top + "px";
            clearInterval(timeid);
          }
        }, 10);
      } else if (parseInt(this.listStyle.top) < top) {
        clearInterval(timeid);
        let timeid = setInterval(() => {
          this.setTop(parseInt(this.listStyle.top) + topSize);
          if (parseInt(this.listStyle.top) >= top) {
            this.listStyle.top = top + "px";
            clearInterval(timeid);
          }
        }, 10);
      }
    },
    setTop(position) {
      this.listStyle.top = `${position}px`;
      this.listStyle["-webkit-top"] = `${position}px`;
      this.listStyle["-ms-top"] = `(${position}px`;
    },
    itemClick(index) {
      this.currentIndex = index;
      this.move(30, -(index - 1) * this.windomH);
    },
    abClick() {
      this.currentIndex++;
      this.upmove(this.currentIndex);
    },
    proClick(){
      this.currentIndex += 2;
      this.upmove(this.currentIndex);
    }
  },
  mounted() {
    this.listEl = this.$refs.list;
    this.listStyle = this.listEl.style;
    this.listStyle.top = 0;
    //监听按下事件
    document.onkeydown = this.keyDom;
    this.windomH = window.innerHeight;
  },
};
</script>

